:root {
  --white: #fff;
  --background-white: #f7f6fb;
  --gray: #a8b3ce;
  --blue: #0a63f2;
  --light-blue: #2b97e8;
  --dark-blue: #2e3eb5;
  --black: #373037;
  --brun: #cd8e24;
  --red: #c73d26;
  --space-sm: 0.5rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
}

@font-face {
  font-family: 'Lato';
  src: url(/pokemon-app/static/media/Lato-Regular.9919edff.ttf);
}
html, body {
  font-family: 'Lato', serif;
  overflow-x: hidden;
  scroll-behavior: smooth;
  margin: 0;
  color: #373037;
  color: var(--black);
}


.header {
  grid-area: header;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: var(--white);
  padding: 1rem;
}
.header .search-input {
  position: relative;
  display: none;
  margin-left: 1rem;
}
.header input {
  position: relative;
  display: flex;
  margin-left: 1rem;
  padding: 0.5rem 1rem 0.5rem 3rem;
  border-radius: 10px;
  font-size: 14px;
  border: 1px #f5f5f5 solid;
  color: #9898aa;
  outline: none;
  background: #fafafa;
  width: 26vw;
}

.header .search-icon {
  display: grid;
  cursor: pointer;
  place-items: center;
}

.header .search-icon svg {
  position: absolute;
  z-index: 1;
  left: 2.5rem;
  width: 1.25rem;
  height: 1.25rem;
  fill: #9090a4;
}

.header .profile {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.header .profile img {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
}

.header .profile h2 {
  font-size: 12px;
  font-weight: 600;
  line-height: 1.5;
}

.header .profile p {
  font-size: 12px;
  color: var(--gray);
}

@media screen and (min-width: 60rem) {
  .header .search-input {
    display: flex;
  }
  .header {
    justify-content: space-between;
  }
  .header .profile h2,
  .header .profile p {
    font-size: 14px;
  }
  .header .profile img {
    width: 2.5rem;
    height: 2.5rem;
  }
}

.sidebar {
  position: absolute;
  z-index: 9;
  grid-area: sidebar;
  height: 100vh;
  width: 9rem;
  display: flex;
  background-color: var(--white);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: var(--space-md);
  text-align: left;
  transition: all 0.2s ease-in;
}
.menu-icon {
  position: absolute;
  display: flex;
  top: 1rem;
  left: 0.5rem;
  width: 2rem;
  align-items: center;
}

.menu-icon svg,
.menu-close svg {
  fill: var(--light-blue);
}

.sidebar.is-closed {
  transform: translateX(-11rem);
}
.sidebar .logo {
  display: flex;
  grid-gap: 4px;
  grid-gap: 4px;
  gap: 4px;
  flex-wrap: wrap;
  align-items: center;
}

.sidebar .logo img {
  display: none;
  width: 2rem;
}

.sidebar h1 {
  font-weight: 600;
  color: var(--light-blue);
}

.sidebar nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 2.5rem;
}

.sidebar nav a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  grid-gap: var(--space-sm);
  gap: var(--space-sm);
  text-decoration: none;
  text-align: center;
  text-decoration: none;
  line-height: 1.5;
  color: var(--gray);
  padding: var(--space-sm) var(--space-md);
  margin: var(--space-sm);
}

.sidebar nav a[aria-current='page'] {
  background-color: var(--light-blue);
  color: var(--white);
  border-radius: 10px;
}
.sidebar nav a svg {
  width: 1.5rem;
  fill: var(--gray);
}

.sidebar nav a[aria-current='page'] svg {
  fill: var(--white);
}

@media screen and (min-width: 60rem) {
  .sidebar {
    position: relative;
    width: 12em;
    padding: var(--space-md) var(--space-xl);
    transform: translateX(0) !important;

  }
  .menu-icon,
  .menu-close {
    display: none;
  }
  .sidebar .logo img {
    display: flex;
  }
}

.app {
  min-height: 100vh;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  display: grid;
  grid-template-areas: 'sidebar header' 'sidebar content';
}
.content {
  grid-area: content;
  padding: 1.5rem;
  background-color: var(--background-white);
  border-top-left-radius: 10px;
  overflow: auto;
}

