.type-page .pokemon-pagination .left {
  transform: rotate(180deg);
}

.type-page .pokemon-pagination svg .st0 {
  fill: var(--black);
}

.type-page .pokemons {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
}

.type-page .no-result {
  width: 21rem;
}

@media screen and (min-width: 60rem) {
  .type-page .pokemons {
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}

